import React, { Component } from 'react';
import { Helmet } from 'react-helmet';
import './App.css';
import './css/main.css';
import Table from './Table';
import Form from './Form';
import sharepost from './sharepost.png';

class App extends Component {
  state = {
    characters: []
  };

  removeCharacter = (index) => {
    const {characters} = this.state;

    this.setState({
      characters: characters.filter((character, i) => {
        return i !== index
      })
    })
  }

  handleSubmit = (character) => {
    this.setState({characters: [...this.state.characters, character]});
  };

  render(){
    const { characters } = this.state;
    
    return (
      <div className="App">
        <Helmet
          meta={[
            {
              property: 'og:url',
              content: 'https://app.dev.vp.demo.eastwest.live/dashboard/credits/earn-credits',
            },
            {
              property: 'og:type',
              content: 'website',
            },
            {
              property: 'og:title',
              content: 'Earn Credit',
            },
            {
              property: 'og:description',
              content: 'How much does culture influence creative thinking?',
            },
            {
              property: 'og:image',
              content: `${window.location.origin}/${sharepost}`,
            },
            {
              property: 'fb:app_id',
              content: '942643939925036',
            },
          ]}
        />
        <h1 className="site-heading">Hello, React!</h1>
        <div className="container">
          <Table 
            characterData={characters}
            removeCharacter={this.removeCharacter} />
          <Form handleSubmit={this.handleSubmit}/>
        </div>
      </div>
    );
  }
}

export default App;
